body {
  background-color: rgba(119, 0, 0, .333);
  padding: 50px;
}

.search-form, .conteiner {
  flex-direction: row;
  justify-content: center;
  align-items: flex-end;
  gap: 20px;
  padding: 10px;
  display: flex;
}

input {
  width: 200px;
  height: 32px;
  text-align: center;
  color: #510909;
  background: #f1ebe5;
  border: none;
  font-weight: 800;
  box-shadow: 0 6px 8px #c4b59d, 0 -2px 1px #fff;
}

button.search-btn {
  width: 52px;
  height: 32px;
  cursor: pointer;
  color: #ab803c;
  border: none;
  margin-top: 15px;
  font-size: 14px;
  font-weight: 700;
  display: inline-block;
  box-shadow: inset 0 3px 4px #fff, inset 0 -3px 4px #c4b59d;
}

button.load-more {
  width: 90px;
  height: 32px;
  cursor: pointer;
  color: #ab803c;
