  font-weight: 700;
  box-shadow: inset 0 3px 4px #fff, inset 0 -3px 4px #c4b59d;
}

button.go {
  width: 150px;
  height: 32px;
  cursor: pointer;
  color: #ab803c;
  border: none;
  font-size: 14px;
  font-weight: 700;
  box-shadow: inset 0 3px 4px #fff, inset 0 -3px 4px #c4b59d;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 15px;
  margin-top: 25px;
  margin-bottom: 20px;
  display: flex;
}

